import React, {Suspense} from "react";
import { graphql, useStaticQuery } from "gatsby";

const WeddingNavBar = React.lazy(() => import('./components/weddingNavbar'));
const BasicNavBar = React.lazy(() => import('./components/basicNavbar'));

const SuspenseWeddingNavBar = (props) => {
    const {hasLottie = false} = props;
    const lottieContainerHeight = hasLottie ? 250 : 0,
        navContainerHeight = 184;

    const height = navContainerHeight + lottieContainerHeight;
    return (
        <div style={{height: `${height}px`, width: '100%'}} />
    );
};

function LazyWeddingNavBar(props) {
    return (
        <Suspense fallback={<SuspenseWeddingNavBar hasLottie={true} />}>
        </Suspense>
    );
}

function LazyBasicNavBar(props) {
    return (
        <Suspense fallback={<SuspenseWeddingNavBar hasLottie={true} />}>
            <BasicNavBar {...props} />
        </Suspense>
    );
}


export const Menu = (props) => {
    const { curRoute, route } = props;
    const data = useStaticQuery(graphql`
        query {
            weddingMenu: googleDocs(name: {eq: "WeddingMenu"}) {
                childrenMarkdownRemark {
                    html
                    htmlAst
                }
            }
            defaultMenu: googleDocs(name: {eq: "Menu"}) {
                childrenMarkdownRemark {
                    html
                    htmlAst
                }
            }
        }
    `);

    let menu;
    if (route === "WeddingMenu") {
        menu = data.weddingMenu;
    } else {
        menu = data.defaultMenu;
    }

    if (route === "WeddingMenu") {
        return (
            <WeddingNavBar 
                curRoute={curRoute}
                htmlAst={menu.childrenMarkdownRemark[0].htmlAst} />
        );
    }

    return (
        <LazyBasicNavBar 
            curRoute={curRoute}
            htmlAst={menu.childrenMarkdownRemark[0].htmlAst} />
        ); 
};

export default Menu;