import Menu from "./src/layout/menu";

// Wraps every page in a component
export const wrapPageElementShared = (props) => {
    return (
        <>
            {props.props.data && props.props.data.page &&
                <Menu route={props.props.data.page.menu} curRoute={props.props.location.pathname} />
            }
            {props.element}
            {/* TODO: <Scripts /> */}
        </>
    );
};